import React, { useState } from "react";
import {
  InputNumber,
  Upload,
  Button,
  List,
  Divider,
  Input,
  Card,
  DatePicker,
  Space,
  Slider,
  Switch,
  Progress,
  Spin,
  Select,
} from "antd";
import { SyncOutlined, UploadOutlined } from "@ant-design/icons";
import { Address, Contract, Balance, EtherInput, AddressInput, Hint, RaiseTile } from "../components";
import { parseEther, formatEther } from "@ethersproject/units";
import { Alert } from "antd";
import { getUrlVars, ipfs, ipfsLinkFromHash, truncateString } from "../helpers";
import { useExternalContractLoader, useContractReader, useBalance } from "../hooks";
import { useParams } from "react-router-dom";

import { create as createIPFSClient } from "ipfs-http-client";
import PawnFloorOracleABI from "../contracts/PawnFloorOracle.abi";
import PawnFloorOracleFactoryABI from "../contracts/PawnFloorOracleFactory.abi";
import { isAddress } from "ethers/lib/utils";
import PawnFloorOracle from "../components/PawnFloorOracle";
import ERC721ABI from "../contracts/ERC721.abi";
//import PawnFloorOracleFactoryAddress from "../contracts/PawnFloorOracleFactory.address";
const PawnFloorOracleFactoryAddress = "0xbd755263A3e31E6C1f83FD72DCc8Fe24b9158e79";
const ipfsClient = createIPFSClient("https://ipfs.infura.io:5001");
const { Option } = Select;
const { TextArea } = Input;
const { RangePicker } = DatePicker;

function Required() {
  return <span style={{ color: "red" }}>*</span>;
}

export default function CreateUI({
  setPurposeEvents,
  address,
  mainnetProvider,
  userSigner,
  localProvider,
  yourLocalBalance,
  price,
  tx,
  writeContracts,
  blockExplorer,
}) {
  const { a } = getUrlVars();
  console.log(`a is ${a}`);
  const [loading, setLoading] = useState(true);
  const [collectionAddress, setCollectionAddress] = useState();
  const [floorPrice, setFloorPrice] = useState(0);
  const inUseProvider = mainnetProvider;
  const factoryInstance = useExternalContractLoader(
    inUseProvider,
    PawnFloorOracleFactoryAddress,
    PawnFloorOracleFactoryABI,
  );
  const instanceAddress = useContractReader(
    { PawnFloorOracleFactory: factoryInstance },
    "PawnFloorOracleFactory",
    "oracles",
    [collectionAddress],
  );

  console.log(`instance address - ${instanceAddress} factory address ${factoryInstance}`);

  function floorDoesntExist() {
    return instanceAddress == "0x0000000000000000000000000000000000000000" || instanceAddress == undefined;
  }

  React.useEffect(() => {
    if (a && a!="") {
      setCollectionAddress(a);
      console.log(`setting collection addrss to ${collectionAddress} ${a}`);
    }
  }, [a]);

   function createFloor() {
    const data = factoryInstance.interface.encodeFunctionData("addOracle", [collectionAddress]);

    tx(
      userSigner.sendTransaction({
        to: PawnFloorOracleFactoryAddress,
        data: data,
        value: parseEther(floorPrice),
      }),
    );
  } 

  return (
    <div>
      <h1>Welcome to the NFT pawn shop:</h1>
      <h4>Enter a collection address to sell or buy:</h4>
      <div style={{ justifyContent: "center", display: "inline-block" }}>
        <div style={{ maxWidth: "250px" }}>
          <AddressInput value={collectionAddress} onChange={e => setCollectionAddress(e)} />
        </div>
      </div>
      {isAddress(collectionAddress) ? (
        floorDoesntExist() ? (
          <>
            <h3>There is no floor for this collection yet. Wanna set one?</h3>
            <div style={{ border: "1px solid #cccccc", padding: 16, width: 400, margin: "auto", marginTop: 64 }}>
              <h2>Set Floor Price</h2>
              <p style={{ color: "grey" }}>
                enter the amount you want to put up as a open floor for any item in the collection{" "}
                <a href={`https://etherscan.io/address/${collectionAddress}`}>{truncateString(collectionAddress, 10)}</a>
              </p>
              <EtherInput value={floorPrice} onChange={e => setFloorPrice(e)} />
              {floorPrice > 0 ? (
                <>
                  <br />
                  <Button onClick={() => createFloor()}>Set Floor</Button>
                  <Hint hint={"funds are safu"} />
                </>
              ) : (
                <>Enter a floor price greater than 0</>
              )}
            </div>
          </>
        ) : (
          <>
            
           <PawnFloorOracle
           oracleAddress={instanceAddress}
           address={address}
           tx={tx}
           inUseProvider={inUseProvider}
           userSigner={userSigner}
           writeContracts={writeContracts}
           />
          </>
        )
      ) : (
        <>
          <br />
          Enter collection address above
        </>
      )}
      <br /> <br /> <br /> <br />
    </div>
  );
}
