function Required() {
  return <span style={{ color: "red" }}>*</span>;
}

export default function PawnFloorOracle({ oracleAddress, address, inUseProvider, userSigner, tx, writeContracts }) {
  const [loading, setLoading] = useState(true);
  const [newFloorPrice, setNewFloorPrice] = useState();
  const [tokenId, setTokenId] = useState();
  const instance = useExternalContractLoader(inUseProvider, oracleAddress, PawnFloorOracleABI);
  const underwriter = useContractReader({ PawnFloorOracle: instance }, "PawnFloorOracle", "underwriter", []);
  const floorPrice = useContractReader({ PawnFloorOracle: instance }, "PawnFloorOracle", "floor", []);
 const oracleFloor = useBalance(inUseProvider, oracleAddress);
  console.log(`${floorPrice} is floor price, oracle floor is ${oracleFloor}`);
  console.log(`${address} is adddress ${underwriter} is underwriter`);
  React.useEffect(() => {}, []);

  function updatePrice() {
    const data = instance.interface.encodeFunctionData("updateFloor", []);

    try {
        tx(
            userSigner.sendTransaction({
              to: oracleAddress,
              data: data,
              value: parseEther(newFloorPrice),
            }),
          );
    } catch (e) {
        alert(e.data.message);
    }
    
  }

  function withdraw() {
    const data = instance.interface.encodeFunctionData("withdraw", []);

    try {
        tx(
            userSigner.sendTransaction({
              to: oracleAddress,
              data: data,
              value: 0,
            }),
          );
    } catch (e) {
        alert(e.data.message);
    }
     
  }

  function acceptOffer() {
    const data = instance.interface.encodeFunctionData("sellFloor", [tokenId]);

    try {
        tx(
            userSigner.sendTransaction({
              to: oracleAddress,
              data: data,
              value: 0,
            }),
          );
    } catch (e) {
        alert(e.data.message);
    }
  }

  return (
    <div>
      <div style={{ border: "1px solid #cccccc", padding: 16, width: 400, margin: "auto", marginTop: 64 }}>
        <h2>
          Best I can do is <b>{formatEther(floorPrice ? floorPrice : "0")}</b>
        </h2>
        <label>Token Id to sell</label>
        <Input value={tokenId} onChange={e => setTokenId(e.target.value)} />
        <Button onClick={()=>acceptOffer()}>Accept Offer</Button>

        <hr />
      </div>
      <div style={{ border: "1px solid #cccccc", padding: 16, width: 400, margin: "auto", marginTop: 64 }}>
        <h4>Underwriter Options</h4>

        <EtherInput value={newFloorPrice} onChange={e => setNewFloorPrice(e)} />
        {address == underwriter ? <Button onClick={()=>withdraw()}>Withdraw</Button> : <></>}

        <Button onClick={()=>updatePrice()}>Update Price</Button>
      </div>
    </div>
  );
}
